import { Link, useLocation } from "react-router-dom";

const Footer = () => {
  const location = useLocation();
  return (
    <footer>
      {location.pathname !== "/Task-Tracker/about" && (
        <>
          <p>Copyright &copy; 2021</p>
          <Link to="/Task-Tracker/about">About</Link>
        </>
      )}
    </footer>
  );
};

export default Footer;
